.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: #fff;
  height: 68px;
  padding: 0 15px;
}

.App-link {
  color: #61dafb;
}

.Header-menu{
    list-style: none;
    margin: 0;
    padding: 0;
    font-family: 'Brown';
    font-weight: 100;
    font-size: 11px;
}
.Header-menu li{
    display: inline-block;
    margin: 0 10px;
    text-transform: uppercase;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
